/* ----------------------------------------------------- */

/* Styling for NavBar Component */

/* ----------------------------------------------------- */

.nav__opener {
  position: fixed;
  top: 0;
  width: 100%;
  height: 54px;
  transition: height 2s;
  overflow: hidden;
  z-index: 9999;
}

/* ----------------------------------------------------- */

/* Styling for Tooltip Component */

/* ----------------------------------------------------- */

.tooltip {
  position: relative;
  display: inline-block;
  height: 100%;
  width: 100%;
  z-index: 999;
  opacity: 1;
  overflow: visible;
}

/* Tooltip text */

.tooltip .tooltip__text {
  visibility: hidden;
  background-color: rgb(75, 108, 193);
  color: #fff;
  text-align: center;
  width: 120px;
  padding: 5px 0;
  border-radius: 6px;
  /* Position the tooltip text */
  position: absolute;
  z-index: 1;
  margin-left: -60px;
}

/* Bottom Postion Specific Styling */

.tooltip .tooltip__text--bottom {
  top: 100%;
  left: 50%;
}

/* Top Position Specific Styling */

.tooltip .tooltip__text--top {
  bottom: 100%;
  left: 50%;
}

/* Show the tooltip text when you mouse over the tooltip container or click it */

.tooltip--hoverable:hover .tooltip__text {
  visibility: visible;
}

/* Show the tooltip text when you select (click or tab to) the tooltip container element */

.tooltip--clickable:focus-within .tooltip__text {
  visibility: visible;
}

.tooltip .tooltip__text--top::after {
  content: " ";
  position: absolute;
  top: 100%; /* At the bottom of the tooltip */
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: rgb(75, 108, 193) transparent transparent transparent;
}

.tooltip .tooltip__text--bottom::after {
  content: " ";
  position: absolute;
  bottom: 100%; /* At the top of the tooltip */
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent rgb(75, 108, 193) transparent;
}

/* Styling for tooltip modal */

.tooltip__modal {
  max-width: 75vw;
  margin: auto;
}

/* ----------------------------------------------------- */

/* Styling for About section component */

/* ----------------------------------------------------- */

/* This class has a Media Query version for mobile size */

.about__text-container {
  display: flex;
  align-items: center;
  min-height: 100vh;
}

.about__header {
  font-size: 5em;
}

.about__text {
  font-size: 2em;
}

/* This class has a Media Query version for mobile size */

.about__img-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
}

.about__img {
  border-radius: 0.5em;
}

@media only screen and (max-width: 767px) {
  .about__text-container {
    display: flex;
    align-items: center;
    min-height: 0;
    padding-top: 2em;
    padding-bottom: 0;
  }

  .about__img-container {
    min-height: 0;
    display: flex;
    align-items: center;
  }
}

/* ----------------------------------------------------- */

/* Styling for Portfolio Section */

/* ----------------------------------------------------- */

.portfolio__hr {
  width: 100%;
  height: 1px;
  color: black;
  background-color: black;
}

/* ----------------------------------------------------- */

/* Styling for Project Card component */

.project__container {
  max-height: 300px;
  margin-bottom: 1em;
}

.project__container:focus {
  outline: none;
}

.project__details {
  position: relative;
  background-color: rgb(75, 109, 193);
  overflow: hidden;
  width: 100%;
  height: 50vw;
  max-height: 300px;
  border-radius: 20px;
  padding: 2em;
  color: white;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.project__details--grey {
  box-shadow: none;
}

.project__overlay {
  position: absolute;
  visibility: visible;
  top: 0;
  left: 0;
  background-color: white;
  overflow: hidden;
  width: 100%;
  height: 100%;
  border-radius: 20px;
  /* Make overlay slightly larger than what it's covering to avoid weird looking corners */
  transform: scale(1.1, 1.1);
  transform-origin: center;
}

/* This doesn't work on IE/Edge, so we're doing it with JS as well, but the JS version creates a flicker, so we're doing both */

.project__container:focus-within .project__overlay {
  visibility: hidden;
}

.project__overlay--hidden {
  visibility: hidden;
}

.project__img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.project__img--grey {
  -webkit-filter: grayscale(100%);
  filter: grayscale(100%);
}

.project__button-container {
  position: absolute;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: space-around;
  padding-bottom: 2em;
  width: 100%;
}

.project__title {
  position: absolute;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 50vw;
  max-height: 300px;
  border-radius: 20px;
  padding-top: 1em;
  color: rgba(0, 0, 0, 0.856);
  -webkit-text-fill-color: rgba(0, 0, 0, 0.856);
  -webkit-text-stroke-width: 0.3px;
  -webkit-text-stroke-color: white;
  text-align: center;
  font-size: 2em;
  font-weight: 800;
  text-shadow: -1px 1px 2px rgba(255, 255, 255, 0.418),
    1px 1px 2px rgba(255, 255, 255, 0.418),
    1px -1px 0 rgba(255, 255, 255, 0.418),
    -1px -1px 0 rgba(255, 255, 255, 0.418);
}

.project__title--grey {
  position: absolute;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 50vw;
  max-height: 300px;
  border-radius: 20px;
  padding-top: 1em;
  color: rgb(139, 139, 139);
  -webkit-text-fill-color: rgb(126, 126, 126);
  -webkit-text-stroke-width: 0.3px;
  -webkit-text-stroke-color: white;
  text-align: center;
  font-size: 2em;
  font-weight: 800;
  text-shadow: -1px 1px 2px rgba(255, 255, 255, 0.418),
    1px 1px 2px rgba(255, 255, 255, 0.418),
    1px -1px 0 rgba(255, 255, 255, 0.418),
    -1px -1px 0 rgba(255, 255, 255, 0.418);
}

.project__title-inner {
  font-size: 1.25em;
}

@media only screen and (max-width: 767px) {
  /* Shrink title text on overlay */

  .project__title-inner {
    font-size: 0.5em;
  }

  /* Removed dead space */

  .project__details {
    overflow: none;
    padding-top: 0.75em;
    padding-left: 5%;
    padding-right: 5%;
    margin: 0;
  }

  /* Shrink title text on details */

  .project__details h3 {
    font-size: 1em;
    text-align: center;
    margin: 0;
  }

  /* On mobile, only the first three lines of description will be show, since that's all that fits. */

  .project__details p {
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

/* ----------------------------------------------------- */

/* Styling for button drawer component */

.drawer__outer {
  position: relative;
  /* height: 15vh; */
}

.plus__button {
  position: relative;
  text-decoration: none;
  background: none;
  border: none;
  width: 5em;
  text-align: left;
  margin-top: 0;
  padding-top: 0.3em;
  border: 3px solid white;
}

.plus__label {
  margin-top: 0;
  padding-bottom: 5px;
}

.plus__button:focus {
  border: 3px solid lightblue;
  border-radius: 1em;
  outline: none;
}

.plus__inner {
  position: absolute;
  top: 18%;
  left: 2.3em;
  background: none;
  border: none;
  border-radius: 100%;
  z-index: 5;
  font-size: 1em;
  margin-left: 0.3em;
}

.plus__icon {
  color: black;
  transform: rotate(0deg);
  transition: transform 0.5s;
}

.plus__button:hover .plus__icon {
  transform: rotate(45deg) !important;
}

.drawer__inner {
  position: relative;
  height: 0vh;
  max-height: 28vh;
  transition: height 2s, transform 2s;
  overflow: hidden;
}

/* ----------------------------------------------------- */

/* Styling for technology filter icons */

.tech {
  overflow: visible;
  position: relative;
  padding: 0.1em;
  background: none;
  max-height: 18vh;
  border-radius: 5%;
  margin-bottom: 3px;
  align-self: stretch;
}

.tech--highlight {
  border-bottom: 3px solid rgb(75, 108, 193);
  margin-bottom: 0px;
}

.tech__centerer {
  height: 100%;
  align-self: stretch;
  display: flex;
  align-items: center;
  justify-content: center;
}

.tech__image--grey {
  -webkit-filter: grayscale(100);
  filter: grayscale(100);
}

/* ----------------------------------------------------- */

/* Styling for contact section */

/* ----------------------------------------------------- */

/* Section hr */

.contact__hr {
  width: 100%;
  height: 1px;
  color: black;
  background-color: black;
}

/* Special text area for use with the email popup */

#contact__email {
  border: none;
  overflow: auto;
  outline: none;
  background: none;
  color: inherit;
  overflow-wrap: break-word;
  box-shadow: none;
  resize: none;
  min-width: 28ch;
}

#contact__email:focus {
  -webkit-text-decoration-line: underline;
  text-decoration-line: underline;
  -webkit-text-decoration-style: dotted;
  text-decoration-style: dotted;
  -webkit-text-decoration-color: white;
  text-decoration-color: white;
}

/* Alternate text area styling for use with white background */

#contact__email--blue {
  border: none;
  overflow: auto;
  outline: none;
  background: none;
  color: inherit;
  overflow-wrap: break-word;
  box-shadow: none;
  resize: none;
  padding: 0;
}

#contact__email--blue:focus {
  -webkit-text-decoration-line: underline;
  text-decoration-line: underline;
  -webkit-text-decoration-style: dotted;
  text-decoration-style: dotted;
  -webkit-text-decoration-color: rgb(75, 108, 193);
  text-decoration-color: rgb(75, 108, 193);
}

/* ----------------------------------------------------- */

/* Styling for contact cards */

.contact__container {
  max-height: 300px;
  margin-bottom: 1em;
}

.contact__card {
  position: relative;
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  max-height: 300px;
}

.contact__image {
  display: block;
  width: 50%;
  height: 50%;
  margin: auto;
  object-fit: cover;
}

.contact__link {
  width: 100%;
  height: 100%;
  background: none;
  border: none;
}

/* Styling for modal buttons for email modal (only shows up on mobile) */

.modal__button--blue {
  background-color: rgb(75, 108, 193);
  color: white;
}

.modal__button--blue:focus {
  border: 3px solid rgba(153, 173, 223, 0.753);
}

/* In order to cover the whole screen, we use position fixed and 100% viewport height and width */

/* To help us vertically and horizontally center our modal, we apply display flex and align & justify center to this background as well */

/* The z-index of 9999 is probably overkill, but it helps us make sure that the modal and background always cover the rest of the website */

.modal__background {
  /* visibility: hidden; */
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

/* The rest of this css is fairly arbitrary and should be changed to fit your specific use-case */

.modal__container {
  position: relative;
  display: flex;
  max-width: 90vw;
  max-height: 50vh;
}

.modal {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  border: 1px solid black;
  border-radius: 7px;
  background-color: white;
  padding: 1em;
}

.modal__header {
  flex-flow: row nowrap;
  flex-basis: 100%;
  width: 100%;
  height: calc(10% - 1.5em);
  justify-content: right;
  margin-bottom: 0.5em;
}

.modal__x {
  flex-flow: column;
  justify-self: right;
  float: right;
  color: black;
  font-weight: 300;
  font-family: Arial, sans-serif;
}

.modal__body {
  flex-flow: row;
  max-height: calc(80% - 2.5em);
  width: 100%;
  overflow: hidden;
}

.modal__title {
  flex-flow: column;
  font-weight: bold;
  font-size: 1.1em;
}

.modal__text {
  flex-flow: column;
  max-height: 90%;
  overflow: auto;
  margin: 0;
  padding: 0;
}

.modal__footer {
  flex-flow: row;
  justify-content: space-between;
  flex-basis: 100%;
  height: calc(10% - 1.5em);
  min-height: 35px;
  width: 100%;
}

.modal__button {
  flex-flow: column;
  border: 1px solid black;
  border-radius: 5px;
  padding: 3px 4px;
  margin: 3px;
}

.modal__button:focus {
  background-color: darkgray;
}

